
import React, {Component} from 'react';

class Projects extends Component {

    state = {
        projs: []
    }

    componentDidMount(){ // busca dados na api pública do github
        const url = 'https://api.github.com/users/apguilherme/repos';
        fetch(url)
            .then(response => response.json())
            .then(json => {
                const jsonSorted = json.sort((o1, o2) => { // ordena o json recebido pela data de criação de repositório
                    return new Date(o2.created_at) - new Date(o1.created_at);
                });
                this.setState({ projs: jsonSorted });
                console.log(this.state.projs);
            });
    }

    render(){
     
        return (
            <div>

                <p className="topic"><span className='topicfocus'>projects</span></p>

                <p className='obs'><i>*this data was retrieved using the Github public API.</i></p>
                    
                { this.state.projs.map(item => (

                    <div className="projectitem">
                                    
                        <p className="">
                            <a href={item.html_url} rel="noopener noreferrer" target='_blank'>{item.name} </a>
                            <span className="subsubtopic">({item.created_at.split('T')[0].split('-')[0]})</span>
                        </p>

                        <div className="projectitemspace">

                            <p className="subsubtopic">{item.description}</p>
                                {/* { 
                                    ( !item.name.includes('backend') ) 

                                    ? (<div><img class="img" src={`https://ik.imagekit.io/acheiapp/githubpage/${item.name}.png`} alt="project"></img></div>)

                                    : (<div></div>)
                                } */}
                            
                        </div>

                    </div>
                )) }
            
            </div>
        )
    }
}

export default Projects;
